body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.carousel {
    position: relative;
    overflow: hidden;
}

.carousel__nav {
    position: absolute;
    top: 50%;
    left: 10px;
    background: url(/react-carousel/static/media/left.d5c29f9f.svg) center center / contain;
    width: 63px;
    height: 63px;
    margin-top: -31px;
    border: none;
    cursor: pointer;
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}

.carousel__nav.carousel__next {
    right: 10px;
    left: auto;
    background: url(/react-carousel/static/media/right.fc0e4e5a.svg) center center / contain;
}

.carousel__nav:hover,
.carousel__nav.carousel__next:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

img {
    width: 100%;
}


.carousel__pagination {
    position: absolute;
    bottom: 10px;
    left:0;
    right: 0;
    text-align: center;
}

.carousel__pagination button {
    display: inline-block;
    width: 15px;
    height: 15px;
    background-color: #000;
    opacity: .8;
    border-radius: 10px;
    border: none;
    margin: 0 4px;
    cursor: pointer;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.carousel__pagination button.active,
.carousel__pagination button:hover {
    background: #fff;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.slide-right-enter-active {
  -webkit-animation: slideRightIn .5s;
          animation: slideRightIn .5s;
}

.slide-right-leave-active {
  -webkit-animation: slideRightOut .5s;
          animation: slideRightOut .5s;
  position: absolute;
  left:0;
  top:0;
  bottom:0;
  right:0;
  width: 100%;
}

@-webkit-keyframes slideRightIn {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slideRightIn {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes slideRightOut {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@keyframes slideRightOut {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

/* left */

.slide-left-enter-active {
  -webkit-animation: slideLeftIn .5s;
          animation: slideLeftIn .5s;
}

.slide-left-leave-active {
